/* color vars
 * may not be totally x-browser safe but yolo
 */
html {
  --darkBlue: #2C3E50;
  --red:      #E74C3C;
  --white:    #ECF0F1;
  --skyBlue:  #3498DB;
  --blue:     #2980B9;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

button {
  -webkit-appearance: none;
  color: var(--white);
  background: none;
  border: none;
  outline: none;
}

html, body {
  background: var(--darkBlue);
  color: var(--white);
  font-size: 16px;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


h1 {
  color: var(--red);
}

.App {
  text-align: center;
  color: var(--white);
}

.input-box {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 0 2rem;
  background: var(--darkBlue);
}

.input-box .label {
  font-size: 1.5rem;
  margin: 0 0 0.5rem;
}

.input-box .input {
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
  width: 90%;
  padding: 12px;
  font-size: 30px;
  height: 56px;
  border: none;
  background: var(--skyBlue);
  border-radius: 4px;
  color: white;
}

.input-box.locked .input {
  background: var(--blue);
}

.input-box.focus {
  position: absolute;
  top: 0;
  height: 100%;
}

.input-box.focus .input {
  background: var(--red);
  outline: none;
}


.keypad {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-left: 1px solid var(--white);
  border-bottom: 1px solid var(--white);
  height: 50%;

  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 1;
}

.keypad-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1 0 33%;
      flex: 1 0 33%;
  background: var(--skyBlue);
  border: 1px solid var(--white);
  border-bottom: none;
  border-left: none;
  font-size: 32px;
  font-weight: 800;
}

.calc-form {
  position: relative;
}



/**
 * inputbox pre-populated values.
 *
 */

.input-box-pre-values .btn-choose {
  display: block;
  width: 100%;
  padding: 1em;
  font-size: 1.25rem;
  color: var(--skyBlue);
}


.input-box-pre-values.focus {
  z-index: 2; /* above keypad */
  background: var(--darkBlue);
}

.input-box-pre-values .list .list-item {
  display: block;
  width: 100%;
  padding: 1em;
  font-size: 1.5rem;
  background: var(--blue);
}

.input-box-pre-values .list .list-item:nth-child(even) {
  background: var(--skyBlue);
}



